@mixin reset {
  margin: 0;
  padding: 0;
}

@mixin list-reset {
  margin: 0;
  padding: 0;

  list-style: none;
}

@mixin d-flex ($wrap: nowrap, $direction: row, $justify: flex-start, $align: center) {
  display: flex;
  flex-wrap: $wrap;
  flex-direction: $direction;
  justify-content: $justify;
  align-items: $align;
}

@mixin abs-pseudo {
  content: "";
  position: absolute;
}

@mixin abs-center {
  position: absolute;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);
}
