.main-navigation {
  @include reset;
  @include d-flex (nowrap, column, flex-start, stretch);

  background-color: $extra-color;

  &--no-js {
    & .main-navigation__toggle {
      visibility: hidden;
    }
  }

  &--closed {
    background-color: rgba(0, 0, 0, 0.3);

    & .main-navigation__list-wrapper {
      display: none;
    }

    & .main-navigation__toggle {
      visibility: visible;
    }
  }
}

.main-navigation__top-bar {
  display: flex;
  justify-content: space-between;
  padding: 22px 20px 25px 20px;
  width: 280px;
  margin: 0 auto;
}

.main-navigation__list {
  @include list-reset;
  @include d-flex (nowrap, column, flex-start, stretch);
}

.main-navigation__item {
  text-align: center;

  border-top: 1px solid #4a5662;
  border-bottom: 1px solid #010101;

  &:first-of-type {
    box-shadow: 0 -1px 0 #000000;
  }
}

.main-navigation__link {
  display: block;
  padding-top: 15px;
  padding-bottom: 15px;

  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  color: #ffffff;
  text-transform: uppercase;

  &:not(&--active):hover,
  &:not(&--active):focus {
    color: $main-color;
  }

  &:not(&--active):active {
    color: rgba($main-color, 0.3);
  }
}

.main-navigation__toggle {
  padding: 0;

  font-size: 0;
  color: #ffffff;

  background-color: transparent;
  border: none;
  cursor: pointer;

  &:hover,
  &:focus {
    color: $main-color;
  }

  &:active {
    color: $main-color;

    opacity: 0.3;
  }

  & .main-navigation__toggle-image--burger {
    display: none;
  }

  & .main-navigation__toggle-image--cross {
    display: block;
  }

  &--closed {
    & .main-navigation__toggle-image--burger {
      display: block;
    }

    & .main-navigation__toggle-image--cross {
      display: none;
    }
  }
}

@media (min-width: $tablet-width) {
  .main-navigation__top-bar {
    width: 620px;
    min-height: 64px;
  }
}

@media (min-width: $desktop-width) {
  .main-navigation {
    background-color: rgba(0, 0, 0, 0.3);

    &--closed {
      & .main-navigation__list-wrapper {
        display: block;
      }
    }
  }

  .main-navigation__wrapper {
    @include d-flex (nowrap, row, space-between, center);

    width: 940px;
    margin: 0 auto;
    padding: 24px 10px 23px 10px;
  }


  .main-navigation__top-bar {
    @include reset;

    width: auto;

    border: none;
  }

  .main-navigation__item:first-of-type {
    box-shadow: none;
  }

  .main-navigation__list {
    @include d-flex (wrap, row, space-between, stretch);

    width: 620px;
  }

  .main-navigation__toggle {
    display: none;
  }

  .main-navigation__item {
    border: none;
  }

  .main-navigation__link {
    padding-top: 10px;
    padding-bottom: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
    text-transform: none;

    &--active {
      border-bottom: 2px solid #ffffff;
    }

    &:not(&--active):active {
      color: rgba(255, 255, 255, 0.3);
    }
  }
}
